.toggle-btn {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 25px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;

  &:focus {
    outline: none;
  }

  .toggle-btn-line {
    width: 30px;
    height: 2px;
    background-color: white;
  }
}
